/* src/components/MusicPlayer.css */

/* Global Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Arial', sans-serif;
    /* Default font */
}

body {
    background: #f0e4d7;
    /* Soft background color */
}

/* Base Styles */
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

/* Music Player Styles */
.music-player {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    width: 90%;
    max-width: 800px;
    margin: auto;
    border-radius: 20px;
    background: linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5));
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.6);
    position: relative;
    overflow: hidden;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

/* Add subtle animation to the music player */
.music-player::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.1);
    opacity: 0.5;
    pointer-events: none;
    animation: pulse 8s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.05);
        opacity: 0.7;
    }

    100% {
        transform: scale(1);
        opacity: 0.5;
    }
}

.music-player h2 {
    margin: 20px 0;
    font-size: 2.5em;
    color: #ffffff;
    text-align: center;
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.7);
}

/* YouTube player styles */
.yt-player {
    border-radius: 15px;
    overflow: hidden;
    width: 100%;
    /* Full width for responsive design */
    max-width: 640px;
    /* Limit the width */
    margin-bottom: 20px;
    /* Spacing below the video */
    transition: transform 0.3s ease;
}

.yt-player:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.5);
}

/* Control Buttons */
.controls {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px 0;
}

.controls svg {
    margin: 0 15px;
    cursor: pointer;
    color: #00bcd4;
    width: 50px;
    height: 50px;
    transition: transform 0.3s ease, color 0.3s ease;
    filter: drop-shadow(0 0 10px rgba(0, 255, 255, 0.7));
    /* Add glow effect */
}

.controls svg:hover {
    transform: scale(1.2);
    color: #03a9f4;
}

.add-song {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px 0;
}

.add-song input {
    padding: 10px;
    margin: 5px 0;
    border: 2px solid #007bff;
    border-radius: 5px;
    width: 90%;
    /* Full width */
    transition: border-color 0.3s ease;
    /* Smooth transition */
}

.add-song input:focus {
    border-color: #0056b3;
    /* Darker blue on focus */
}

.add-song button {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
    /* Smooth transition */
}

.add-song button:hover {
    background-color: #0056b3;
    /* Darker blue on hover */
}

/* Playlist Styles */
.song-list {
    margin-top: 20px;
    width: 100%;
}

.song-list h3 {
    margin-bottom: 15px;
    font-size: 2em;
    color: #ffffff;
    text-align: center;
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.7);
}

.song-list ul {
    list-style-type: none;
    padding: 0;
    width: 100%;
    /* Ensure the list takes full width */
}

.song-list li {
    padding: 12px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    color: #cccccc;
    border-radius: 5px;
    position: relative;
    border: 2px solid transparent;
    /* For border animation */
    margin-bottom: 10px;
    /* Space between items */
}

.song-list li:hover {
    background-color: rgba(0, 0, 0, 0.3);
    transform: scale(1.05);
    border: 2px solid #00bcd4;
    /* Add border highlight */
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.5);
}

.song-list li.active {
    font-weight: bold;
    color: #00bcd4;
    /* Highlight active song */
}

/* Theme Styles */
/* Light Theme */
.light-mode {
    background-color: #ffffff;
    color: #333333;
}

/* Dark Theme */
.dark-mode {
    background-color: #1e1e1e;
    color: #ffffff;
}

/* Responsive Design */
@media (max-width: 768px) {
    .music-player {
        width: 95%;
        /* Make it responsive */
        padding: 15px;
    }

    .controls svg {
        margin: 0 8px;
        width: 35px;
        height: 35px;
    }

    .song-list h3 {
        font-size: 1.5em;
    }
}

/* Full-Screen Button Styles */
.full-screen-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: #00bcd4;
    transition: transform 0.3s ease;
}

.full-screen-button:hover {
    transform: scale(1.1);
    color: #03a9f4;
}

/* Dark Mode Toggle Button */
button {
    background-color: #00bcd4;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 1em;
    margin: 10px 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    position: relative;
    overflow: hidden;
}

button::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300%;
    height: 300%;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0.5s ease;
    z-index: 0;
}

button:hover::after {
    transform: translate(-50%, -50%) scale(1);
}

button:hover {
    background-color: #03a9f4;
    transform: translateY(-2px);
    color: #ffffff;
    /* Ensure text is visible on hover */
}